@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
*
{
  box-sizing: border-box;
  padding: 0;
  margin: 0; 
  font-family: "Montserrat", sans-serif;
}
.active::before
{
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  padding: 0.5px;
  white-space: nowrap;
  bottom: -14px;
  left: 0;
  background-color: white;
  transform-origin: center;
  transform: scaleX(1.4);
  transition: transform 0.25s ease-out ;
  z-index: 2;
}
nav
{
  position: sticky;
  top: 0;
  margin-top: -10px;
  z-index: 3;
  padding: 30px;
}
nav .menu
{
  position: absolute;
  top: 1rem;
  left: 1rem;
  display: none;
  flex-direction: column;
  /* justify-content: space-around; */
  width: 2.25rem;
  height: 2rem;
  gap: 10px;

}
nav .menu span
{
  height: 1rem;
  width: 100%;
  background-color: white;
  border-radius: 0.2rem;
}
.nav
{
  background-color: #991622;
}
.nav ul li
{
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5em;
}
.nav ul li Link
{
  color: white;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
}
.menu-items
{
  position: relative;
  color: white;
}
.menu-items::after
{
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  padding: 0.5px;
  /* size: 10px; */
  white-space: nowrap;
  /* offset: 100; */
  bottom: -14px;
  z-index: 4;
  left: 0;
  background-color: white;
  transform-origin: center;
  transform: scale(0);
  transition: transform 0.25s ease-out ;
}
.menu-items:hover::after
{
  transform: scaleX(1.4);
  height: 2px;
}

@media (max-width : 480px) {
  nav .menu
  {
    display: flex;
    top: 1.5rem;
  }
  nav ul li
  {
    margin-top: 30px;
    padding-bottom: 30px;
    justify-content:end;
    flex-direction: column;
    align-items: flex-start;
    /* margin-top: 20px; */
  }
}