
footer
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 450px;
    margin-bottom: -20px;
    overflow: hidden;
    
}
.footer-background-overlay
{
    position: relative;
}
.footer-background-overlay > img
{
    width: 100%;
    position: relative;
    height: 450px;
    object-fit: cover;
    /* z-index: 0; */
}
.footer-background-overlay::after
{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 450px;
    background-color: rgb(153, 22, 34, 0.8);
    z-index: 0;
}
.footer-fixed
{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}
.top-footer-logo img
{
    height: 60px;
    width: 200px;
}
.top-footer
{
    display: flex;
    gap: 30px;
}
li
{
    list-style: none;
}
.footer-social-icons ul
{
    /* display: inline-flex; */
}
.footer-social-icons ul li
{
    list-style: none;
}
.top-footer-text
{
    word-break: break-all;
    color: white;
    padding: 15px 0px;
}
.footer-social-icons
{
    font-size: 2em;
    display: inline-flex;
    gap: 20px;
}
.footer-social-icons ul li
{
    display: inline-flex; 
    gap: 10px;
}

.top-footer-section-2, .top-footer-section-3, .top-footer-section-4
{
    width: 200px;
    color: white;
}
.footer-menu a
{
    color: white;
}
.footer-menu ul
{
    padding-top: 25px;
}
a
{
    /* color: white; */
    text-decoration: none;
}
.top-footer li
{
    line-height: 30px;
}
@media only screen and (max-width: 600px) {
    .top-footer
    {
        flex-direction: column;
    }
    .footer-fixed
    {
        
    }  
}
