
*{
    padding: 0;
    margin: 0;
 
}
:root
{
    --primary-color: #991622;
}
.image1
{
    width: 100%;
    height: 100vh;
    object-fit: cover;
    background-repeat: no-repeat;
}
.background
{  
    min-width: 100%;
}
.background-overlay
{
    position: relative;   
}
.background-overlay::after
{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    
}
.container
{
    position: relative;
    text-align: center;
    z-index: 0;
    overflow: hidden;
}

.welcome-info
{
    position: absolute;
    margin-top: -35px;
    top: 50%;
    color: white;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}
.welcome-info h1
{   
    font-size: 2.5vw;
    overflow: hidden;
    white-space: nowrap;
    padding-bottom: 40px;
}

.about-us-btn
{   
    display: inline;
    padding: 15px 30px 15px 30px;
    background-color: var(--primary-color);
    border-radius: 30px;
    transition: all 0.3s ease-in-out;
    font-size: 17px;
    color: white;
}
.about-us-btn:hover
{
    background-color: #FFFFFF;
    color: var(--primary-color);
    cursor: pointer;
}
.boxes
{
    /* z-index: 3; */
    /* color: black; */
    z-index: 1;
    margin-top: -120px;
    /* text-align: center; */
}

.box-container
{
    
    overflow: hidden;
    /* margin-top: -40px; */
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    padding: 80px 120px;
    background-color: white;

}
.box
{
    position: relative;
    margin-top: 0px;
    background-color: white;
}

.box-container-wrapper
{
    /* margin-top: -50px; */
    /* text-align: center; */
    border: 1px solid rgb(178, 178, 178);
    /* padding: 80px 120px; */
    box-shadow: 0px 0px 2px -5px rgba(0,0,0,0.75);
    z-index: 1;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* z-index: 3; */
    overflow: hidden;
    height: 250px;
    width: 300px;
}
.box h3
{
    font-size: 22px;
}
.box:hover
{
    margin-top: -40px;
    padding: -20px;
}
.boxes .icon
{
    font-size: 100px;
    /* height: 80px; */
    /* width: 100%; */
    padding: 20px;
    color: white;
    /* margin: 20px; */
    border-radius: 50%;
    background-color: var(--primary-color);
}
.box
{
    transition: all 0.3s ease;
    position: relative;
    z-index: 0;
}
.box-container-wrapper::before
{
    content: "";
    width: 6em;
    height: 6em;
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    top: 28%;
    left: 36%;
    background-color: var(--primary-color);
    z-index: 0;
    transform: translate(-55% ,-55%);
    transform: scale(0);
    transition: all 0.4s ease-in-out;

}
.box:hover .box-container-wrapper::before
{
    /* height: 100%; */
    transform: scale(6);
    /* z-index: 0; */
    
    color: white;
}
.box:hover .box-container-wrapper
{
    /* width: 300px;
    height:300px; */
}
.box:hover h3, .box-icon
{
    color: white;
    z-index: 1;
}

.box:hover .icon
{
    color: white;
}

.box-container-wrapper:hover
{
    padding: 80px 120px;

}
.about-us-content
{
    padding-bottom: 40px;
}
.about-us-background-overlay
{
    position: relative;
    margin-bottom: 15px;
    overflow: hidden;
}
.about-us-background-overlay::after
{
    content: url('./img/backgroundoverlay1.jpg');
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 500px;
    z-index: -1;
    /* background-color: red; */
    opacity: 0.2;
    overflow: hidden;
    white-space: nowrap;
    /* background-color: rgba(255,0,0,0.75); */
}
.about-us-header h1
{
    color: #000;
    font-size: 2.5em;
    margin-bottom: 20px;
}

.about-us-icon
{
    font-size: 3em;
    color: var(--primary-color);
}
.about-us-section
{
    display: flex;
    justify-content: center;
    align-items: center;
}    
.about-us-section img
{
    height: 400px;
    width: 100%;
    object-fit: contain;
}
.about-us-right-section
{
    text-align: justify;
    padding: 0px 30px 30px 30px;
    background-color: white;
    border: 1px black solid;
    box-shadow: rgba(0, 0, 0, 0.5);
    margin-left: -50px;
}
.about-us-right-section h2
{
    padding: 30px 0px 30px 0px;
    color: var(--primary-color);

}
.about-us-right-section p
{
    margin-bottom: 30px;
}
.about-us-right-section-button
{
    padding: 10px;
    margin-bottom:  20px;
    background-color: var(--primary-color);
    border-radius: 5px;
    color: white;
    cursor: pointer;
    display: inline-flex;
    /* transform: scale(0); */
    transition: all 0.5s ease;  
    /* position: relative;   */
}
.about-us-right-section-button:hover
{
    transform: scale(1.1);
}
.section-services
{
    width: 100%;
}
.services-header
{
    font-size: 2rem;
    padding: 30px 0px;
}
.cards
{
    margin-top: -20px;
}
.card-container
{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 120px 100px 120px;
    overflow: hidden;
   
}
.card-section
{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px;
    /* padding: 100px 100px; */
}

.card
{
    position: relative;
    padding: 80px 40px 80px 40px;
    background-color: white;
    transition: all 0.7s ease;
    width: 350px;
    overflow: hidden;
    /* transform: scale(0); */
}

.card::before
{
    position: absolute;
    height: 5px;
    bottom: 0;
    left: 0;
    content: '';
    width: 100%;
    transform: scale(0);
    background-color: var(--primary-color);
}
.card:hover
{
    margin-top: -20px;
    margin-bottom: 20px;
    background-color: white;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-left: 1px solid #B2B2B2;
    border-right: 1px solid #B2B2B2;
    border-top: 1px solid #B2B2B2;
    /* box-shadow: 1px -1px 35px -16px rgba(0,0,0,0.5); */
}
.card:hover::before
{
    transform: scale(1);
}
@media only screen and (max-width: 600px) {
    .box-container
    {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .box
    {
        width: 100%;
    }
    .box:hover
    {
        padding: 0;
        margin: 0;

    }
    .container
    {
        width: 100%;
        /* overflow: hidden; */
    }
    nav ul li
    {
        overflow: hidden;
    }
    .box-container
    {
        padding: 0;
        display: flex;
        padding: 0;
        justify-content: center;
    }
    .box-container-wrapper
    {
        width: 100%;
        margin-top: 0;
    }
    .card-container
    {
        /* overflow: hidden; */
    }
    .card-section
    {
        display: flex;
        flex-direction: column;
    }
    .about-us-section
    {   
        flex-direction: column;
    }
    .about-us-left-section
    {
        display: none;
    }
    .about-us-right-section
    {
        width: 100%;
        margin: 0;
    }
    .about-us-right-section h2
    {
        text-align: left;
    }
    .welcome-info h1
    {
        font-size: 20px;
    }
    
}