.header
{
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 15px;
  font-optical-sizing: auto;
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: .5em;
  border-bottom: 1px solid white;
  box-shadow: 7px 16px 37px -18px rgba(0,0,0,0.67);

}
.header ul li
{
  list-style: none;
}
.header ul li a
{
  text-decoration: none;
  color: white;
  padding: 1em;
  color: #666B70;
}
.site-logo
{
  height: 67px;
  width: 212px;
}
.contact-info .icon
{
    color: #991622;
    margin-right: 6px;
    margin-top: 6px;
    font-size: 16px;
}
.contact-info-phone
{
    position: relative;
}   
.contact-info-phone::before
{
    position: absolute;
    left: 0;
    content: url("../svg/phone.svg");
}
.social-icons .icon
{
    font-size: 25px;
}
.facebook-icon
{
    color: #4267B2;
}
.twitter-icon
{
    color: #24A4F2;
}

.linkedin-icon
{
    color: #007EBB;
}
.social-icons
{
    margin-top: 10px;
    transition: all 0.2s;
}
.social-icons .facebook-icon
{
    transition: all 0.2s ease-in-out;
}
.social-icons .facebook-icon:hover
{
    transform: scale(1.1);
    opacity: 0.8;
}
.social-icons .twitter-icon
{
    transition: all 0.2s ease-in-out;
}
.social-icons .twitter-icon:hover
{
    transform: scale(1.1);
    opacity: 0.8;
}
.social-icons .linkedin-icon
{
    transition: all 0.2s ease-in-out;
}
.social-icons .linkedin-icon:hover
{
    transform: scale(1.1);
    opacity: 0.8;
}

@media only screen and (max-width: 600px) {
    .header
    {
        gap: 25px;
        flex-wrap: nowrap;
        justify-content: space-between;
        overflow: hidden;
    }
    .header img
    {
        width: 180px;
        height: 60px;
    }
    .header .contact-info
    {
        display: flex;
        flex-direction: column;
        gap: 2px;
        font-size: 12px;
    }
    .header .contact-info .icon
    {
        font-size: 12px;
    }
    .social-icons
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 10px;
    }
    
    
}
